import React, {useState, useEffect} from "react";
import "./App.css";
import waste from '../img/icons8-waste.png';

  function Day({name}) {
    
    const [userInput, setUserInput] = useState("");
    function getInputValue (event) {
        event.preventDefault()
        setUserInput(event.target.value); 
      }


    const [dayList, setDayList] = useState([]);
    function addToList(index) {
        if(userInput === "") return (alert("Empty task, please wright something!"))
        setDayList([...dayList, {id: index, text: userInput}])
        setUserInput("")
      }


    function handleKeyPress(event) {
        if (event.keyCode === 13) {
        event.preventDefault();
        addToList();
       }
      }


    const completeTodo = (index) => {
      const newTodos = [...dayList];
      setDayList(newTodos);
      newTodos[index].isCompleted = !newTodos[index].isCompleted
      console.log(newTodos[index].isCompleted)
    }
    
    
    const removeTodo = (index) => {
      const newTodos = [...dayList];
      setDayList(newTodos);
      newTodos.splice(index, 1);
    };


    const [tasksRemaining, setTasksRemaining] = useState(0);
    useEffect(() => { setTasksRemaining(dayList.filter(todo => !todo.isCompleted).length) }, [dayList]);
    //[dayList u useEffect hook je parametar kada će se renderirati dom]

    let summary;
    if (tasksRemaining === 0) {
        summary = 'You have no tasks for today.'
    } else if (tasksRemaining === 1) {
         summary = `You have ${tasksRemaining} task for today.`
    } else if (tasksRemaining > 1) {
      summary = `You have ${tasksRemaining} tasks for today.`
    } 
        
  //index, event... je samo držač vrijednosti u listi, parametar 
  //if-else statements mogu ići samo izvan jsx za koji se mogu koristiti ternary expressions 
  //summary mora biti let zbog promjene vrijednosti  

    return (
        <div className="Day">

          <div className="day-header">
            <h3 className="day-name">{name}</h3>
            <div className="summary" >{summary}</div>            
            <div className="day-input">
                <input type="text" name="userInput" value={userInput} placeholder="tasks for today..."
                onChange={getInputValue} onKeyDown={handleKeyPress} />
                <div className="add" onClick={addToList}>Add</div>
            </div>
          </div>

          <div className="day-list">
            <ul > 
              {dayList.map((todo, index) => (
                <li key={index} index={index} todo={todo} >
                  <span className="check-btn" onClick={() => completeTodo(index)}>Done</span>
                    <span className={todo.isCompleted ? "done" : "not-done"}>{todo.text}</span>
                  <img className="trash-img" src={waste} alt="waste icon" onClick={() => removeTodo(index)} />
                </li>
              ))} 
            </ul>
          </div>

    </div> 
     )
    }

export default Day;


